import "./css/styles.css";
console.log("log from js");

const toggle = document.querySelector("#theme-switch-toggle");

const Theme = {
  LIGHT: "light-theme",
  DARK: "dark-theme",
};
const STORAGE_KEY = "theme";

function checkStorage() {
  let currentTheme = localStorage.getItem(STORAGE_KEY);

  if (currentTheme) {
    toggle.checked = currentTheme === Theme.DARK ? true : false;
  } else {
    currentTheme = Theme.LIGHT;
  }

  document.body.classList.add(currentTheme);
}

checkStorage();

function onToggleChange(e) {
  if (toggle.checked) {
    document.body.classList.add("dark-theme");
    document.body.classList.remove("light-theme");
    localStorage.setItem(STORAGE_KEY, Theme.DARK);
  } else {
    document.body.classList.remove("dark-theme");
    document.body.classList.add("light-theme");
    localStorage.setItem(STORAGE_KEY, Theme.LIGHT);
  }
}

toggle.addEventListener("change", onToggleChange);
